import React from "react";
import PT from "prop-types";
import Layout from '../../../components/layout'
import SEO from "../../../components/seo"
import styles from "./styles.module.css"

const splitRegex = /[\s.?]+/;

class CharsHistogram extends React.PureComponent {
  
  static propTypes = {
    text: PT.string.isRequired,
    countWhitespaces: PT.bool,
  }

  static defaultProps = {
    countWhitespaces: false,
  }

  getHistogram = () => {
    const { text, countWhitespaces } = this.props;
    let histogram = text
      .split('')
      .reduce((acc, char) => {
          if (!countWhitespaces && char === " ") {
            return acc;
          }
          acc[char] = char in acc ? acc[char] + 1 : 1;
          return acc;
      }, {});
  
    return Object
      .entries(histogram)
      .sort((a, b) => a[1] < b[1] ? 1 : -1)
  }

  render() {
    
    return (
      <div>
        <h1>Characters histogram</h1>
        <table>
          <tbody>
          {
            this.getHistogram().map(entry => (
              <tr key={entry[0]}>
                <td>{entry[0]}</td>
                <td>{entry[1]}</td>
              </tr>
            ))
          }
          </tbody>
        </table>
      </div>
    )
  }
}

const WordsStats = ({
  words = 0,
  charCount = 0, 
  charsWithoutSpaces = 0,
  spaces = 0
}) => {
  return (
    <div>
      <h1>Stats</h1>
      <table>
        <tbody>
          <tr><td>Words</td><td>{words}</td></tr>
          <tr><td>Characters (With Spaces)</td><td>{charCount}</td></tr>
          <tr><td>Characters (Without Spaces)</td><td>{charsWithoutSpaces}</td></tr>
          <tr><td>Spaces</td><td>{spaces}</td></tr>
        </tbody>
      </table>
    </div>
  )
}

const WordRow = ({word, count}) => (
  <tr>
    <td>{word}</td>
    <td>{count}</td>
  </tr>
);

class WordsHistogram extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      howMuchToShow: 50,
      showAll: false,
      error: null,
    };
  }

  hideAll = () => this.setState({ showAll: false });
  showAll = () => this.setState({ showAll: true });
  toggleShowAll = () => this.setState({ showAll: !this.state.showAll });
  howMuchChange = (e) => {
    if (e.target.value === "") {
      e.target.value = 0;
    }
    const tryParse = parseInt(e.target.value)
    if (isNaN(tryParse)) {
      this.setState({ error: 'Set positive natural number (for example 50)'})
    } else {
      this.setState({
        error: null,
        howMuchToShow: tryParse,
      })
    }
  };

  render() {
    const { entries } = this.props;
    return (
      <div>
        <h1>Words Histogram</h1>
        <div>ShowAll 
          <input 
            type="checkbox" 
            className="margin-left"
            checked={this.state.showAll}
            onChange={this.toggleShowAll}
          />
        </div>
        {!this.state.showAll && (
          <div>How much words to show? 
           <input
              className="margin-left"
              type="number" 
              onChange={this.howMuchChange}
              value={this.state.howMuchToShow === 0 ? "" : this.state.howMuchToShow}
           />
          </div>
        )}
        <table>
          <thead>
            <tr>
              <th><b>Word</b></th><th><b>Count</b></th>
            </tr>
          </thead>
          <tbody>
          {
              entries
              .slice(0, this.state.howMuchToShow)
              .map(([word, count]) => <WordRow word={word} count={count} key={word} />)
          }
          </tbody>
        </table>
      </div>
      )
    };
} 

class CountWordsPage extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      text: "",
      words: 0,
    }
  }

  computeWords = text => 
    text.split(splitRegex).filter(word => word !== "" || word !== " ").length;

  computeWordsHistogram = text => {
    const words = text.split(splitRegex);
    const lowercasedWords = words.map(w => w.toLowerCase())
    const wordsCounter = lowercasedWords.reduce((acc, word) => {
      acc[word] = (word in acc) ? acc[word] + 1 : 1;
      return acc;
    }, {});
    
    const sortedWordsHistogram = 
      Object.entries(wordsCounter)
      .sort((entryA, entryB) => entryA[1] < entryB[1] ? 1 : -1)

    return sortedWordsHistogram;
  }

  timeoutFn = (text) => {
    this.setState({
        words: this.computeWords(text),
    });
  };

  textAreaChangeHandler = (e) => {
    const { value } = e.target;

    // delete previous processor
    clearTimeout(this.delayedProcessor);

    // create new
    this.delayedProcessor = setTimeout(() => this.timeoutFn(value), 2);

    this.setState({
      text: value,
    })
  };

  charsWithoutSpaces = (text) => {
    const newText = text.replace(/\s/g, '')
    return newText.length
  }

  render() {
    const charsWithoutSpaces = this.charsWithoutSpaces(this.state.text)
    return (
      <Layout withFooter={false}>
        <div>
          <SEO
            title={"Words Counter"}
            description={"Words counter and characters counter"}
            keywords={["words", "counter", "histogram"]}
          />
          <div>Paste your text to the input below. </div>
          <div><b>Words:</b> {this.state.words}</div>
          <textarea className={styles.textarea} onChange={this.textAreaChangeHandler} />
          <WordsStats
            words={this.state.words}
            charCount={this.state.text.length}
            charsWithoutSpaces={charsWithoutSpaces}
            spaces={this.state.text.length - charsWithoutSpaces}
          />
          <WordsHistogram entries={this.computeWordsHistogram(this.state.text)} />
          <CharsHistogram text={this.state.text} />
        </div>
      </Layout>
    );
  }
 }

export default CountWordsPage;
